@import url(https://fonts.googleapis.com/css?family=Sacramento);
.homePage{
    width:100%;
    height:100%;
    position: absolute;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-attachment: inherit;
}

.music{
    float:left;
}

.info{
    position: absolute;
    font-family: Brush Script MT, Brush Script Std, cursive;
    text-align: center;
    color:transparent;
    background: -webkit-linear-gradient(top left, rgb(22, 183, 204), yellow);
    background: linear-gradient(to bottom right, rgb(22, 183, 204), yellow);
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-transform-origin:center;
            transform-origin:center;
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
    font-size:70px;
    top:70%;
    right:5%;
}
@media screen and (max-width: 600px) {
    .homePage{
        background-size:600px 600px;
    }
    .info{
        top:60%;
        font-size:40px;
    }
}

.info:hover{
    font-size:80px;
    opacity: 0;
 
}
.container-NavBar{

    width:100%;
    height:80px;
    line-height: 80px;
    background-color:rgba(0,0,0,0.2);
    z-index: 10;
}
.container-NavBar .Name{
    height:100%;
    float:left;
    margin-left:3%;
    -webkit-transform-origin:center;
            transform-origin:center;
    -webkit-transition: all 0.5s linear;
    transition: all 0.5s linear;
    text-decoration: none;
    vertical-align: middle;
}
.container-NavBar .Name.HRH{
    font-family:'Sacramento';
    font-size:50px;
    color:transparent;
    background: -webkit-linear-gradient(top left, red, yellow);
    background: linear-gradient(to bottom right, red, yellow);
    background-clip:text;
    -webkit-background-clip: text;
    opacity: 0.7;
}

.container-NavBar .Name.UNSW{
    font-family: monospace,Gill Sans Extrabold,;
    margin-left:60%;
    font-size:30px;
    color:transparent;
    background: -webkit-linear-gradient(top left, yellow,red);
    background: linear-gradient(to bottom right, yellow,red);
    -webkit-background-clip: text;
            background-clip: text;
}
.container-NavBar .nav{
    right:20px;
    height:100%;
    float:right;
}

.container-NavBar .nav.logo{
    right:20px;
    height:100%;
    float:right;
}


.container-NavBar .Name:hover{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.container-NavBar #dropdown{
    display: none;
    font-size:30px;
    color:white;
    list-style-type: none;
    text-align: center;
    float:right;
    padding-right:5%;
}
.container-NavBar #dropdown li .icon{
    color:white;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}
.container-NavBar #dropdown li .icon:hover{
    color: violet;;
    font-size:35px;
}
@media screen and (max-width: 600px) {
    .container-NavBar #dropdown{
        display: block ;
    }
    .container-NavBar .nav{
        float:left;
        display: none;
    }
    .container-NavBar .nav.logo{
        float:left;
        display: none;
    }
}
.component-navigation{
    width:100%;
    height:100%;
}
.component-navigation .Nav{
    width:100%;
    height:100%;
    float:left;
    
}
.component-navigation ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    height:100%;
}

.component-navigation ul li a{ 

    font-size:30px;
    display:block;
    color:white;
    text-align: center;
    padding:5% 20px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    text-decoration: none;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;

}

.component-navigation li a:hover:not(.active) {
    font-size:35px;

    color: violet;
}

.component-navigation .Nav.horizontal ul li{
    float: left;
    height:100%;
}


.component-navigation #menu{
    display: block ;
}

.component-navigation #dropdown .icon{
    margin-right:0;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
    
}
.component-navlogo{
    width:100%;
    height:100%;
}

.component-navlogo .logos{
    right:0;
}
.component-navlogo .logos ul{
    
    list-style-type: none;
    margin: 0;
    padding: 0;
    height:100%;
}
.component-navlogo .logos ul li{
    margin-top:3%;
    float:left;
    height:100%;
}
.component-navlogo .logos ul li a{
    margin-top:3%;
    margin-right:30px;
}

.component-navlogo .logos ul li a img{
    width:50px;
    height:50px;
    -webkit-transition: all 0.2s linear;
    transition: all 0.2s linear;
}

.component-navlogo .logos ul li a:hover:not(.active) img{
    width:60px;
    height:60px;
}


@-webkit-keyframes color-change {
    0%{
        color: #f35626;
    }
    to {
        color: #feab3a;
    }
    100%{
        color: #f35626;
    }
  }
@keyframes color-change {
    0%{
        color: #f35626;
    }
    to {
        color: #feab3a;
    }
    100%{
        color: #f35626;
    }
  }
.container-player{
    position: absolute;
    width:100%;
    height: 0;
}

.container-player.with-cover{
    padding-bottom: 100%; 
}

.container-player .header{
    left:5%;
    position: absolute;
    float: left;
    border-radius:50%;
    width:70px;
    height:70px;
    border-style: double;
    display:flex;
    align-items: center;
    justify-content: center;
    -webkit-transition:all 0.5s linear;
    transition:all 0.5s linear;
    box-shadow: 2px 2px 4px 4px orangered;
    flex-direction: column;
    overflow: hidden;
    z-index: 1;
}

.container-player.with-cover .header{
    margin-top:40%;
    right: 5%;
}

.container-player .header .progress-bar{
    position:absolute;
}

.container-player .header .middle-control{
    display:flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    overflow: hidden;
    z-index: 3;
}
.container-player .header:hover, .header:focus, .header:active
{
    box-shadow: 15px 15px 20px rgba(0,0, 0, 0.4);

    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}

.time{
    display: flex;
    border-color: white;
    color:white;
    width:100%;
}

.duration{
    flex: 1 1;
    float:left;
}

.currentTime{
    flex:1 1;
    float:left;
}

@-webkit-keyframes ROTATE {
    from {
        -webkit-transform: rotate(0deg); 
    }
  
    to {
        -webkit-transform: rotate(360deg); 
    }
  }


@keyframes ROTATE {
    from {
        -webkit-transform: rotate(0deg); 
    }
  
    to {
        -webkit-transform: rotate(360deg); 
    }
  }


.Controls {
    border: 1px solid white;
    border-radius: 50%;
    width:50px;
    height:50px;
    border-style: double;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transition: all 5s ease;
    transition: all 5s ease;
    z-index: 10;
}

.Controls:hover{
    border: 1px solid rgb(56, 169, 214);
    border-style: double;
    -webkit-animation: ROTATE 2s linear infinite;
            animation: ROTATE 2s linear infinite;
}

.Controls .Pause{
    display: inline-block;
    margin:auto;
    width:0;
    height:50%;
    border-style: double;
    border-width: 0px 0 0px 20px;
    border-color: transparent transparent transparent white;
}



.Controls .Pause:hover{
    border-color:blueviolet;
}


.Controls .Start{
    border-style:solid;
    margin-left:9px;
    height:0;
    width:0;
    border-top: 14px solid transparent; 
    border-left: 24.3px solid  white; 
    border-right:0px solid  transparent;
    border-bottom: 14px solid transparent; 
    
}

.Controls .Start:hover{
    border-left-color: blueviolet;
}



#NotDoneYet p{

    color:white;
    margin-top:20%;
    font-size: 40px;
    text-align: center;



}
body{
    background:black;
    width:100%;
    height:100%;
    margin: 0 auto;
    text-align: left;

}

body .logobar{
    width:100%;
    position: fixed;
    display:table-footer-group;
    bottom:0;
}
@-webkit-keyframes rotation{
    from{
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
    }
    to{
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
}



@keyframes rotation{
    from{
      -webkit-transform: rotateY(0deg);
              transform: rotateY(0deg);
    }
    to{
      -webkit-transform: rotateY(360deg);
              transform: rotateY(360deg);
    }
}



.Gallery-container{
    width:100%;
    height:100%;
    position: absolute;
    -webkit-perspective: 2100px;
            perspective: 2100px;
}

.Gallery-container #overlay{
  position: absolute;
  display: none;
  width: 100%;
  height: 100%;
  font-size: 100px;
  -webkit-transform:translateZ(450px);
          transform:translateZ(450px);
  background-color:(0,0,0,1);
  cursor: pointer;
  color:blue;
  
}

.Gallery-container #overlay .prev{
    color:grey;
    font-size: 80px;
    position: absolute;
    left:15%;
    top:30%;
    z-index: 20;
}
.Gallery-container #overlay .prev:hover{
  color:yellowgreen;
}

.Gallery-container #overlay .next{
  color:grey;
  font-size: 80px;
  position: absolute;
  float:right;
  right:15%;
  top:30%;

}

.Gallery-container #overlay .next:hover{
  color:yellowgreen;
}

.Gallery-container #overlay .background{
  width:100%;
  height:100%;
  position: absolute;
  z-index:-1;
}


.Gallery-container #overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #818181;
    display: block;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.Gallery-container #Gallery{
    width:100%;
    height:100%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: rotation 30s infinite linear;
            animation: rotation 30s infinite linear;
}

#Gallery .Photos{
    width: 210px;
    height: 210px;
    line-height: 210px;
    left:40%;
    position: absolute;
    display: block;
    overflow: hidden;
    border: solid 5px black;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    cursor: pointer;
}

#Gallery .Photo:hover {
    cursor: pointer;

}




#photo{
    margin: auto;
    width:100%;
    height:100%;
    max-height:screen;
    clear:both;
    text-align: center;
}
@media screen and (min-height:700px) { 
    #photo{
        height:700;
    }
}
#photo img{
    width:100%;
    height:100%;
}





#PhotoCard {
    position: relative;
    margin: 0 auto;
    width:60%;
    height:85%;
    margin-top:3%;
    border-color:red;
}

#PhotoCard .photo{
    width:70%;
    height:100%;
    float:left;
}
#PhotoCard .close{

    position: absolute;
    font-size:20px;
    right:0.2%;
    color:gray;
    z-index: 2;
  
  }
#PhotoCard .desc{
    position: relative;
    width:30%;
    height:100%;
    float:right;
    background-color: white;
}

#PhotoCard h1{
    margin-top:40px;
    text-align: center;
    font-size:40px;
    color:black;
    font-family: Luminari, fantasy;

}

#PhotoCard .description{
    font-size:20px;
    white-space: pre-line;
    color:black;
    margin-left:5px;
    text-align: left;
    padding:10%;
}


#PhotoCard .Signature{
    left:10px;
    color:blueviolet;
    font-size: 30px;
    text-align: right;
    position:absolute;
    right:10%;
    bottom:0;
    font-family:'Sacramento';
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
}

@media screen and (max-width: 600px) {
    #PhotoCard{
        width:100%;
        margin-top:0;
    }
    #PhotoCard .photo{
        width:100%;
        float:left;
    }
    #PhotoCard .desc{
        width:100%;
        float:left;
    }
    #PhotoCard h1{
        margin-top:0px;
    }
    #PhotoCard .description{
        font-size:20px;
        white-space: normal;
        white-space: initial;
        color:black;
        margin-left:5px;
        text-align: left;
        padding-top:0%;
    }
}

.GalleryPage{
    position: absolute;
    width:100%;
    height:100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.GalleryPage .GalleryContainer{
    position: absolute;
    width:100%;
    height:70%;
}
