@import url('https://fonts.googleapis.com/css?family=Sacramento');
.container-NavBar{

    width:100%;
    height:80px;
    line-height: 80px;
    background-color:rgba(0,0,0,0.2);
    z-index: 10;
}
.container-NavBar .Name{
    height:100%;
    float:left;
    margin-left:3%;
    transform-origin:center;
    transition: all 0.5s linear;
    text-decoration: none;
    vertical-align: middle;
}
.container-NavBar .Name.HRH{
    font-family:'Sacramento';
    font-size:50px;
    color:transparent;
    background: linear-gradient(to bottom right, red, yellow);
    background-clip:text;
    -webkit-background-clip: text;
    opacity: 0.7;
}

.container-NavBar .Name.UNSW{
    font-family: monospace,Gill Sans Extrabold,;
    margin-left:60%;
    font-size:30px;
    color:transparent;
    background: linear-gradient(to bottom right, yellow,red);
    background-clip: text;
}
.container-NavBar .nav{
    right:20px;
    height:100%;
    float:right;
}

.container-NavBar .nav.logo{
    right:20px;
    height:100%;
    float:right;
}


.container-NavBar .Name:hover{
    transform: rotateY(180deg);
}

.container-NavBar #dropdown{
    display: none;
    font-size:30px;
    color:white;
    list-style-type: none;
    text-align: center;
    float:right;
    padding-right:5%;
}
.container-NavBar #dropdown li .icon{
    color:white;
    transition: all 0.2s linear;
}
.container-NavBar #dropdown li .icon:hover{
    color: violet;;
    font-size:35px;
}
@media screen and (max-width: 600px) {
    .container-NavBar #dropdown{
        display: block ;
    }
    .container-NavBar .nav{
        float:left;
        display: none;
    }
    .container-NavBar .nav.logo{
        float:left;
        display: none;
    }
}